<template>
  <section class="hero">
    <div class="hero-body">
      <p class="title">Peikai Tan</p>
      <p class="subtitle">Singapore Population Plot</p>
    </div>
  </section>
  <div class="container">
    <div class="columns">
      <div class="column"></div>
      <div class="column is-three-fifths">
        <svg></svg>
      </div>
      <div id="tooltip" class="column">
        <div class="card-content">
          {{ properties.Name.toLowerCase() }}
          <br />
          Population: {{ properties.Population ? properties.Population : 0 }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "App",
  data() {
    return {
      mapData: null,
      populationData: null,
      maxPopulation: 0,

      properties: {
        Name: "",
        Population: "",
      },
    };
  },
  methods: {
    match(subzone) {
      subzone = subzone.toLowerCase();

      var result = this.populationData.find((obj) => {
        return obj["Subzone"].toLowerCase() === subzone;
      });

      if (result) {
        result = parseInt(result["Population"], 10);
        if (result) {
          this.maxPopulation = Math.max(result, this.maxPopulation);
        }
      }
      return result;
    },
    generateColor(population) {
      if (isNaN(population)) {
        return "white";
      }
      var hexValue = Math.round(
        ((this.maxPopulation - population) / this.maxPopulation) * 255
      ).toString(16);

      if (hexValue.length === 1) {
        hexValue = "0" + hexValue;
      }

      return "#" + hexValue + hexValue + hexValue;
    },
  },
  components: {},
  mounted() {
    let width = 1000,
      height = 640;

    let svg = d3.select("svg").attr("viewBox", "0 0 " + width + " " + height);

    // Load external data
    Promise.all([
      d3.json(
        "https://raw.githubusercontent.com/Chi-Loong/CSC3007/main/assignments/sgmap.json"
      ),
      d3.csv(
        "https://raw.githubusercontent.com/Chi-Loong/CSC3007/main/assignments/population2021.csv"
      ),
    ]).then((data) => {
      this.mapData = data[0];
      this.populationData = data[1];

      this.mapData.features.forEach((element) => {
        element.properties["Population"] = this.match(
          element.properties["Subzone Name"]
        );
      });

      // Map and projection
      var projection = d3
        .geoMercator()
        .center([103.851959, 1.29027])
        .fitExtent(
          [
            [20, 20],
            [980, 580],
          ],
          data[0]
        );

      let geopath = d3.geoPath().projection(projection);

      var tooltip = d3.select("#tooltip").style("opacity", 0);

      svg
        .append("g")
        .attr("id", "districts")
        .selectAll("path")
        .data(this.mapData.features)
        .enter()
        .append("path")
        .attr("d", geopath)
        .attr("stroke", "black")
        .attr("fill", (d) => {
          return this.generateColor(d.properties.Population);
        })
        .on("mouseover", (d, i) => {
          d3.select(d.target)
            .transition()
            .duration("50")
            .attr("opacity", ".85");

          tooltip.transition().duration(50).style("opacity", 1);

          this.properties = i.properties;
        })
        .on("mouseout", (d) => {
          d3.select(d.target).transition().duration("50").attr("opacity", "1");

          tooltip.transition().duration("50").style("opacity", 0);

          this.properties = {
            Name: "",
            Population: "",
          };
        });

      const defs = svg.append("defs");

      var margin = { top: 0, right: 40, bottom: 30, left: 40 };
      var barHeight = 15;
      var colorScale = d3
        .scaleSequential(d3.interpolateRgb("white", "black"))
        .domain([0, this.maxPopulation]);

      let axisScale = d3
        .scaleLinear()
        .domain(colorScale.domain())
        .range([margin.left, width - margin.right]);

      let axisBottom = (g) =>
        g
          .attr("class", `x-axis`)
          .attr("transform", `translate(0,${height - margin.bottom})`)
          .call(
            d3
              .axisBottom(axisScale)
              .ticks(width / 80)
              .tickSize(-barHeight)
          );
      const linearGradient = defs
        .append("linearGradient")
        .attr("id", "linear-gradient");

      linearGradient
        .selectAll("stop")
        .data(
          colorScale.ticks().map((t, i, n) => ({
            offset: `${(100 * i) / n.length}%`,
            color: colorScale(t),
          }))
        )
        .enter()
        .append("stop")
        .attr("offset", (d) => d.offset)
        .attr("stop-color", (d) => d.color);

      svg
        .append("g")
        .attr("transform", `translate(0,${height - margin.bottom - barHeight})`)
        .append("rect")
        .attr("transform", `translate(${margin.left}, 0)`)
        .attr("width", width - margin.right - margin.left)
        .attr("height", barHeight)
        .style("fill", "url(#linear-gradient)");

      svg.append("g").call(axisBottom);
    });
  },
};
</script>

<style>
#tooltip {
  /* position: absolute;
  right: 5%;
  bottom: 5%; */
  text-transform: capitalize;
}

body {
  height: 100vh;
  background-color: lightgray;
}
</style>
